import React, { useEffect } from 'react';
import styled from 'styled-components';
import jquery from 'jquery';
import $ from 'jquery';
window.$ = window.jquery = jquery;

const FooterBlock = styled.div`
#footer {
    clear: both;
    position: relative;
    width: 100%;
    height: auto;
}

.f_info_w {
    position: relative;
    width: 100%;
    float: left;
    background: #2e2f31;
    border-bottom: 1px solid #4a4a4c;
    min-height: 90px;
    z-index: 10;
}

.f_info_w .f_logo {
    position: absolute;
    top: 27px;
    left: 0;
    width: 209px;
    height: 42px;
    background: url("img/m_icon_all.png") no-repeat 0 -368px;
}

.f_menu {
    padding: 35px 0 12px 247px;
}

.f_menu li {
    display: inline-block;
    background: url("img/line_h13.gif") no-repeat left 8px;
}

.f_menu li:first-child {
    background: none;
}

.f_menu li a {
    display: inline-block;
    margin: 0 18px 0 18px;
    font-size: 13px;
    color: #fff;
    text-decoration: none;
}

.f_menu li.privacy a {
    color: #ff9b2f;
}

.f_info_w .f_sns_w {
    position: absolute;
    right: 0;
    top: 36px;
}

.f_info_w .f_sns_w li {
    float: left;
    height: 21px;
    margin: 0 0 0 34px;
}

.f_info_w .f_sns_w li:first-child {
    margin: 0 0 0 0;
}

.f_info_w .f_sns_w li a {
    font-size: 0;
    line-height: 0;
    float: left;
}

.f_info_w .f_sns_w li.sns00 a {
    width: 22px;
    height: 21px;
    background: url("img/m_icon_all.png") no-repeat -95px -418px;
}

.f_info_w .f_sns_w li.sns01 a {
    width: 10px;
    height: 21px;
    background: url("img/m_icon_all.png") no-repeat 0 -418px;
}

.f_info_w .f_sns_w li.sns02 a {
    width: 13px;
    height: 21px;
    background: url("img/m_icon_all.png") no-repeat -16px -418px;
}

.f_info_w .f_sns_w li.sns03 a {
    width: 21px;
    height: 21px;
    background: url("img/m_icon_all.png") no-repeat -33px -418px;
}


/* 2.주소 및 사이트링크 */
.f_link_w {
    position: relative;
    width: 100%;
    float: left;
    background: #2e2f31;
    padding: 30px 0;
}

.f_addr {
    font-size: 13px;
    line-height: 22px;
    color: #c0c0c0;
    padding: 0 450px 0 0;
}

.f_addr .f_mail {
    font-size: 13px;
    line-height: 22px;
    color: #c0c0c0;
    text-decoration: none;
    letter-spacing: 0;
}

.f_addr div {
    margin-right: 22px;
    display: inline-block;
}

.f_link {
    float: left;
    position: absolute;
    right: 0;
    top: 0;
    background: #393a3b;
}

.f_link .dropdown {
    float: left;
    background: #393a3b;
}

.f_link button {
    font-size: 13px;
    color: #d0d0d0;
    display: block;
    width: 170px;
    line-height: 38px;
    height: 38px;
    text-align: left;
    padding: 0 20px;
    background: url("img/icon_moreWs1.png") no-repeat 90% center;
}

.f_link2 {
    position: absolute;
    right: 180px;
    top: 0;
    background: #393a3b;
}

.f_link2 button {
    font-size: 13px;
    color: #d0d0d0;
    display: block;
    width: 140px;
    line-height: 38px;
    height: 38px;
    text-align: center;
    padding: 0 20px;
}

.copyright {
    clear: both;
    letter-spacing: 0px;
}

.btn_top {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 57px;
    right: -80px;
    cursor: pointer;
    background: url("img/btn_top.png") no-repeat 0 0;
    font-size: 0;
    line-height: 0;
}

`;

const Footer = () => {

    useEffect(() => {
        $(window).scroll(function () {
            if ($(this).scrollTop() > 200) {
                $('.btn_top').fadeIn();
            } else {
                $('.btn_top').fadeOut();
            }
        });
    
        $('.btn_top').click(function () 
        {
            $('html').animate({
                scrollTop: 0
            }, 400);
        });
    },[]);

    return (
        <FooterBlock>
              <footer id="footer">
                    <div className="f_info_w">
                        <div className="row">
                            <div className="f_logo"></div>
                            <ul className="f_menu">
                                <li className="privacy">
                                    <a href="#">개인정보처리방침</a>
                                </li>
                                <li>
                                    <a href="#">전자우편무단수집거부</a>
                                </li>
                                <li>
                                    <a href="#">캠핑장 등록안내</a>
                                </li>
                                <li>
                                    <a href="#">미등록야영장불법영업신고</a>
                                </li>
                            </ul>

                            <ul className="f_sns_w">
                                <li className="sns00">
                                    <a href="#" target="_blank" title="고캠핑인스타그램 바로가기">고캠핑 인스타그램</a>
                                </li>
                                <li className="sns01">
                                    <a href="#" target="_blank" title="고캠핑 공식 페이스북 바로가기">고캠핑 공식 페이스북</a>
                                </li>
                                <li className="sns02">
                                    <a href="#" target="_blank" title="고캠핑 공식 블로그 바로가기">고캠핑 공식 블로그</a>
                                </li>
                                <li className="sns03">
                                    <a href="#" target="_blank" title="고캠핑 공식 유튜브 바로가기">고캠핑 공식 유튜브</a>
                                </li>
                            </ul>
                            <div className="btn_top">상단으로</div>
                        </div>
                    </div>

                    <div className="f_link_w">
                        <div className="row">
                            <div className="f_addr">
                                <adivress className="adivr">
                                    <div>강원도 원주시 세계로 10 (반곡동 1914-6) 한국관광공사</div>
                                    <div>TEL : 02-6450-0909 (상담시간 : 평일 10:00~18:00)</div>
                                    <div>
                                        EMAIL : <a href="#"className="f_mail">webmaster@gocamping.or.kr</a>
                                    </div>
                                </adivress>
                                <p className="copyright">
                                    Copyrights &copy; 2018 <span>KOREA TOURISM ORGANIZATION</span>
                                    ALL RIGHTS RESERVED.
                                </p>
                            </div>
                            <div className="f_link2">
                                <button type="button">지자체공무원</button>
                            </div>
                            <div className="f_link">
                                <div className="dropdown">
                                    <button className="f_link_tt dropbtn">관련사이트</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>
        </FooterBlock>
    );
};

export default Footer;