import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import jquery from 'jquery';
import $ from 'jquery';
window.$ = window.jquery = jquery;


const HeaderBlock = styled.div`
    
#wrap {
    overflow: hidden;
    position: relative;
    width: 100%;
    margin: 0;
    padding: 0;
    padding: 0;
}

#lnb_hm {
    position: relative;
    width: 100%;
    height: 36px;
    line-height: 36px;
    background: #f8f8f8;
    border-bottom: 1px solid #e5e5e5;
}

#lnb_hm .row {
    height: 36px;
}

/* 유틸메뉴 */
.utilMenu {
    position: absolute;
    top: 0px;
    right: 0px;
}

.utilMenu .list {
    position: relative;
}

.utilMenu .list>li {
    float: left;
    background: url("img/line_h11.gif") no-repeat left 13px;
}

.utilMenu .list>li:first-child {
    background: none;
}

.utilMenu .list>li>a {
    display: block;
    margin: 0 4px;
    padding: 0 14px;
    color: #333;
    text-decoration: none;
    font-size: 12px;
}

.utilMenu .list>li.sitemap>a {
    padding-right: 0;
}

/* header */
#header {
    position: relative;
    width: 100%;
    height: 149px;
    background: #fff url("img/line_hm184.gif") repeat-x;
    z-index: 990;
}

#header_in {
    position: relative;
    width: 100%;
    height: 149px;
    background: #fff url("img/line_hm184.gif") repeat-x;
    z-index: 990;
}

/* 메인_로고 */
.logoarea {
    position: relative;
    width: 100%;
    height: 84px;
}

#logo {
    position: absolute;
    top: 23px;
    left: 50%;
    margin-left: -103px;
    width: 206px;
    height: 40px;
}

#logo a {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 206px;
    height: 40px;
    background: url("img/m_icon_all.png") no-repeat 0 -327px;
}

.sns_list {
    position: relative;
    display: inline-block;
    margin-top: 26px;
}

.sns_list li {
    float: left;
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
}

.sns_list li a {
    font-size: 0;
    width: 38px;
    height: 38px;
    display: block;
}

.sns_list li.sns00 {
    background: url("img/sns_link.gif") no-repeat 0 0;
    margin-right: 5px;
}

.sns_list li.sns01 {
    background: url("img/sns_link.gif") no-repeat -39px 0;
    margin-right: 5px;
}

.sns_list li.sns02 {
    background: url("img/sns_link.gif") no-repeat -78px 0;
    margin-right: 5px;
}

.sns_list li.sns03 {
    background: url("img/sns_link.gif") no-repeat -117px 0;
    margin-right: 5px;
}

.m_search {
    position: absolute;
    top: 23px;
    right: 0;
    background: #f0f0f0;
    width: 232px;
    height: 38px;
}

.m_search_in {
    width: 175px;
    height: 38px;
    border: 0;
    background: #f0f0f0;
    padding-left: 1em;
}

.search_btn {
    position: absolute;
    top: 3px;
    right: 15px;
    width: 20px;
    height: 20px;
}

.search_btn input {
    background: url("img/m_icon_all.png") no-repeat 0 -31px;
    width: 20px;
    height: 20px;
    border: 0;
    font-size: 0;
    margin-top: 5px;
}

/* 메인메뉴 */
.gnb_in {
    display: block;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #dedede;
    background: #fff;
    position: absolute;
    top: 85px;
}

#gnb .top1menu {
    position: relative;
    float: left;
    z-index: 990;
    width: 100%;
}

#gnb .top1menu>li {
    position: relative;
    float: left;
    width: 25%;
    text-align: center;
    line-height: 64px;
}

#gnb .top1menu>li.depth1>a {
    position: relative;
    display: inline-block;
    font-size: 18px;
    color: #202020;
    line-height: 24px;
    z-index: 991;
}

#gnb .top1menu>li.on>a,
#gnb .top1menu>li.depth1>a:hover {
    padding-bottom: 34px;
    background: url("img/top1menu_on.png") no-repeat center bottom;
    color: #ef760a;
}

#gnb .top1menu>li>.top2menu {
    display: none;
    position: absolute;
    top: 64px;
    left: 0;
    width: 100%;
    height: 301px;
    padding-top: 40px;
    text-align: center;
    z-index: 990;
    border-left: 1px solid #dedede;
}

#gnb .top1menu>li:last-child>.top2menu {
    border-right: 1px solid #dedede;
}

#gnb .top1menu>li>.top2menu>li {
    width: 100%;
    padding: 5px 0;
    line-height: 27px;
}

#gnb .top1menu>li>.top2menu>li>a {
    display: inline-block;
    background: url("img/top2menu.gif") no-repeat left 9px;
    font-size: 15px;
    color: #5d5d5d;
    line-height: 17px;
}

#gnb .top1menu>li>.top2menu>li>a:hover {
    background: url("img/top2menu_on.gif") no-repeat left 9px;
    color: #ef760a;
}

.top2menu_in {
    padding-top: 80px;
    display: block;
    text-align: center;
    margin: 0 auto;
    width: 215px;
}

.top2menu_in li:nth-child(1) {
    width: 217px;
    height: 52px;
    color: #fff;
    line-height: 52px;
}

.top2menu_in li:nth-child(1)>a {
    color: #fff;
    text-decoration: none;
    background: url("img/mn_org01.png") no-repeat;
    display: block;
    width: 217px;
    height: 52px;
    font-size: 16px;
    font-weight: bold;
}

.top2menu_in li:nth-child(2) {
    width: 217px;
    height: 52px;
    color: #fff;
    line-height: 52px;
    margin-top: 15px;
}

.top2menu_in li:nth-child(2)>a {
    color: #fa8d00;
    text-decoration: none;
    background: url("img/mn_org02.png") no-repeat;
    display: block;
    width: 217px;
    height: 52px;
    font-size: 16px;
    font-weight: bold;
}

.wrapBg {
    display: none;
    position: absolute;
    top: 64px;
    left: 0;
    margin-left: 0;
    width: 100%;
    height: 340px;
    background: #fff url("img/sm_bg.png") no-repeat center top;
    z-index: 11;
    border-top: 1px solid #dedede;
    border-bottom: 1px solid #dedede;
}

.bg_black {
    display: none;
    position: fixed;
    top: 524px;
    right: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.6;
    z-index: -100;
}

.inner {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
}

.header_t {
    border-bottom: 1px solid #ddd;
    overflow: hidden;
    padding: 15px 0;
    font-family: "Noto Sans Korean";
}

.header_t .left {
    float: left;
}

.header_t .left>a {
    vertical-align: middle;
}

.header_t .left span {
    display: inline-block;
    vertical-align: middle;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #555555;
    margin: 0 12px 0 27px;
}

.header_t .left ul {
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    background: #EDEDED;
    border-radius: 100px;
    height: 40px;
    padding: 0 16px;
}

.header_t .left ul li {
    float: left;
    line-height: 35px;
    margin-left: 12px;
    padding-left: 12px;
    position: relative;
}

.header_t .left ul li::before {
    content: '';
    width: 1px;
    height: 12px;
    background-color: #ccc;
    position: absolute;
    top: 12px;
    left: 0;
}

.header_t .left ul li:first-child {
    margin: 0;
    padding: 0;
}

.header_t .left ul li:first-child::before {
    width: 0;
}

.header_t .left ul li a {
    font-size: 14px;
    line-height: 14px;
    color: #666666;
}

.header_t .right {
    float: right;
}

.header_t .login {
    width: 128px;
    height: 40px;
    float: right;
    vertical-align: middle;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.15);
    border-radius: 22px;
    margin-left: 10px;
}

.header_t .login>a {
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    line-height: 17px;
    color: #444444;
    padding: 10px 0 10px 16px;
    display: block;
    position: relative;
    background-color: #fff;
    border-radius: 22px;
    border: 2px solid #FD640E;
}

.header_t .login>a:before {
    content: '';
    width: 14px;
    height: 14px;
    background: url(img/ico_login.png) no-repeat 0 0;
    position: absolute;
    top: 11px;
    right: 16px;
}
`;

const Header = () => {

    useEffect(() => {
        $("#gnb").hover(function () {
            $(this).find(".top2menu").show();
            $(this).find(".wrapBg").show();
        }, function () {
            $(this).find(".top2menu").hide();
            $(this).find(".wrapBg").hide();
        });
    },[]);

    return (
        <HeaderBlock> 
               <header className="gnb_header">
                    <div className="header_t">
                        <div className="inner">
                            <div className="left">
                                <a href="#" target="_blank" title="새창">
                                    <img src="img/logo_t.png" alt="한국관광공사"/>
                                </a>

                                <span className="total-login">통합로그인링크</span>

                                <ul>
                                    <li>
                                        <a href="#">대한민국 구석구석</a>
                                    </li>
                                    <li>
                                        <a href="#">열린관광 모두의여행</a>
                                    </li>
                                    <li>
                                        <a href="#">푸드트립코리아</a>
                                    </li>
                                    <li>
                                        <a href="#">고캠핑</a>
                                    </li>
                                    <li>
                                        <a href="#">사진갤러리</a>
                                    </li>
                                </ul>
                            </div>

                            <div className="right">
                                <div className="login">
                                    <a href="#">통합로그인</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>

                <div id="lnb_hm">
                    <div className="row2">
                        <div className="utilMenu">
                            <div>
                                <ul className="list">
                                    <li className="join">
                                        <a href="#">사업자용</a>
                                    </li>

                                    <li className="join">
                                        <a href="#" target="_blank">안전교육</a>
                                    </li>

                                    <li className="sitemap">
                                        <a href="#">사이트맵</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>

                <header id="header">
                    <div id="header_in">
                        <div className="row2">
                            <div className="logoarea">
                                <h1 id="logo">
                                    <a href="#"></a>
                                </h1>

                                <ul className="sns_list">
                                    <li className="sns00">
                                        <a href="#" target="_blank">고캠핑 인스타그램</a>
                                    </li>
                                    <li className="sns01">
                                        <a href="#" target="_blank">고캠핑 공식페이스북</a>
                                    </li>
                                    <li className="sns02">
                                        <a href="#" target="_blank">고캠핑 공식블로그</a>
                                    </li>
                                    <li className="sns03">
                                        <a href="#" target="_blank">고캠핑
                                            공식유튜브</a>
                                    </li>
                                </ul>

                                <div className="m_search">
                                    <form name="totalSearchForm01" action="#" method="get">
                                        <fieldset className="totalSearch">
                                            <legend>통합검색</legend>
                                            <input type="text" className="m_search_in" id="searchKrwd_total" name="searchKrwd"
                                                title="검색어 입력박스" placeholder="검색어를 입력하세요." />

                                            <div className="search_btn">
                                                <input type="submit" value="검색" title="새창" />
                                            </div>
                                        </fieldset>
                                    </form>
                                </div>
                            </div>
                        </div>

                        <nav id="gnb">
                            <div className="gnb_in">
                                <div className="row">
                                    <div className="wrap">
                                        <ul className="top1menu clearfix">
                                            <li className="depth1 41">
                                                <a href="#">캠핑GO</a>
                                                <ul className="top2menu">
                                                    <li>
                                                        <Link to='/Content'>캠핑장 검색</Link>
                                                    </li>
                                                    <li>
                                                        <a href="#">지도로 검색</a>
                                                    </li>
                                                    <li>
                                                        <ul className="top2menu_in">
                                                            <li>
                                                                <a href="#">이달의 추천 캠핑장</a>
                                                            </li>
                                                            <li>
                                                                <a href="#">캠핑장 이벤트</a>
                                                            </li>
                                                        </ul>
                                                    </li>
                                                </ul>
                                            </li>
                                            <li className="depth1 51">
                                                <a href="#">캠핑플러스</a>
                                                <ul className="top2menu">
                                                    <li>
                                                        <a href="#">캠핑소식</a>
                                                    </li>
                                                    <li>
                                                        <a href="#">안전한캠핑즐기기</a>
                                                    </li>
                                                    <li>
                                                        <a href="#">캠핑장 안전수칙 동영상</a>
                                                    </li>
                                                </ul>
                                            </li>
                                            <li className="depth1 53">
                                                <a href="#">고객센터</a>
                                                <ul className="top2menu">
                                                    <li>
                                                        <a href="#">공지사항</a>
                                                    </li>
                                                    <li>
                                                        <a href="#">캠핑장정보 수정요청</a>
                                                    </li>
                                                    <li>
                                                        <a href="#">등록 야영장 확인 문의</a>
                                                    </li>
                                                    <li>
                                                        <a href="#">캠핑장 공지사항</a>
                                                    </li>
                                                </ul>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                                <div className="wrapBg"></div>
                            </div>
                        </nav>
                    </div>
                </header>
        </HeaderBlock>
    );
};

export default Header;